
ol {
    list-style-type: none;
    counter-reset: item;
    margin: 0;
    padding: 0;
  }
  
  ol > li {
    display: table;
    counter-increment: item;
    margin-bottom: 0.6em;
  }
  
  ol > li:before {
    content: counters(item, ".") ". ";
    display: table-cell;
    padding-right: 0.6em;    
  }
  
  li ol > li {
    margin: 0;
  }
  
  li ol > li:before {
    content: counters(item, ".") " ";
  }
  .unordered-cls {
    list-style: none;
  }
  
  .unordered-cls  ::before {
    content: "â€¢";
    font-size: 20px;
    color: #1987FB;
    font-weight: bold;
    display: inline-block; 
    width: 1em;
    margin-left: -1em;
  }
  .unordered-list{
     margin-left: 70px;
  }
  .third-policy-cls{
      width: 80%;
      margin: auto;
  }
 .lists-digit{
